import { useTranslation } from "react-i18next";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../InstituteAnalytic.module.css";

const StudentAttendanceCard = ({ student }) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.staclc_card}
        style={{
          justifyContent: "space-between",
        }}
      >
        <div className={style.staclc_card_container}>
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
          <div className={style.staclc_card_inner}>
            <h6>
              {`${student?.studentFirstName ?? ""} ${
                student?.studentMiddleName ?? ""
              } ${student?.studentLastName ?? ""}`}
            </h6>
            <p>
              {t("gr_number")} {student?.studentGRNO ?? ""}
            </p>
          </div>
        </div>
        <div className={style.stc_stats}>
          <span>{student?.avg_attendance ?? 0}</span>
        </div>
      </div>
    </>
  );
};

export default StudentAttendanceCard;
